import React, { useState } from "react";
import "./Sidebar.scss";
import { SidebarProps } from "../../types/Filters";

export const Sidebar: React.FC<SidebarProps> = ({
  filters,
  onFilterChange,
}) => {
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);

  const handlePriceChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    onFilterChange({
      ...filters,
      priceRange: {
        ...filters.priceRange,
        [name]: Number(value),
      },
    });
  };

  const handleFilterChange = (
    key: keyof typeof filters,
    value: string | boolean
  ) => {
    onFilterChange({
      ...filters,
      [key]: value,
    });
  };

  const resetFilters = () => {
    onFilterChange({
      priceRange: { min: 0, max: 100000 },
      gender: "all",
      ageCategory: "all",
      purpose: "all",
      shape: "all",
      taste: "all",
      hit: false,
    });
  };
  const categories = [
    { value: "all", label: "" },
    { value: "", label: "" },
    { value: "", label: "" },
    { value: " ", label: " " },
    { value: " ", label: " " },
    { value: "   ", label: "   " },
    { value: " (, , )", label: " (, , )" },
    { value: "  ", label: "  " },
    { value: "", label: "" },
    { value: "", label: "" },
    { value: "", label: "" },
    { value: "  ", label: "  " },
  ];

  return (
<div className="sidebar--wrapper">

    {/*  ""      */}
    <button className="sidebar__toggle--btn" onClick={() => setIsSidebarOpen(true)}>
        
      </button>

      {/*      */}
      {isSidebarOpen && <div className="sidebar__overlay" onClick={() => setIsSidebarOpen(false)}></div>}

    <div className={`sidebar ${isSidebarOpen ? "sidebar--open" : ""}`}>
    <div className="sidebar__header">
          <h2 className="sidebar__header--title"></h2>
          <button className="sidebar__close--btn" onClick={() => setIsSidebarOpen(false)}></button>
        </div>


            <div className="sidebar__section">
              <h3 className="sidebar__title"></h3>
              <div className="sidebar__price">
                <input
                  type="number"
                  name="min"
                  value={filters.priceRange.min}
                  onChange={handlePriceChange}
                  className="sidebar__input"
                />
                <span>-</span>
                <input
                  type="number"
                  name="max"
                  value={filters.priceRange.max}
                  onChange={handlePriceChange}
                  className="sidebar__input"
                />
              </div>
              <div className="sidebar__slider">
                <input
                  type="range"
                  name="min"
                  min="0"
                  max={filters.priceRange.max - 100}
                  value={filters.priceRange.min}
                  onChange={handlePriceChange}
                  className="sidebar__range"
                />
                <input
                  type="range"
                  name="max"
                  min={filters.priceRange.min + 100}
                  max="10000"
                  value={filters.priceRange.max}
                  onChange={handlePriceChange}
                  className="sidebar__range"
                />
              </div>
            </div>

            <div className="sidebar__section">
              <h3 className="sidebar__title"></h3>
              <ul className="sidebar__list">
                {["all", "women", "men"].map((gender) => (
                  <li key={gender}>
                    <label>
                      <input
                        type="radio"
                        name="gender"
                        value={gender}
                        checked={filters.gender === gender}
                        onChange={(e) =>
                          handleFilterChange("gender", e.target.value)
                        }
                      />
                      {gender === "all"
                        ? ""
                        : gender === "women"
                        ? " "
                        : " "}
                    </label>
                  </li>
                ))}
              </ul>
            </div>

            <div className="sidebar__section">
              <h3 className="sidebar__title"></h3>
              <ul className="sidebar__list">
                {[
                  { value: "all", label: "" },
                  { value: " 18 ", label: " 18 " },
                  { value: " 18  50 ", label: " 18  50 " },
                  { value: " 50 ", label: " 50 " },
                ].map(({ value, label }) => (
                  <li key={value}>
                    <label>
                      <input
                        type="radio"
                        name="ageCategory"
                        value={value}
                        checked={filters.ageCategory === value}
                        onChange={(e) =>
                          handleFilterChange("ageCategory", e.target.value)
                        }
                      />
                      {label}
                    </label>
                  </li>
                ))}
              </ul>
            </div>

            <div className="sidebar__section">
              <h3 className="sidebar__title"></h3>
              <ul className="sidebar__list">
              {categories.map(({ value, label }) => (
        <li key={value}>
          <label>
            <input
              type="radio"
              name="purpose"
              value={value}
              checked={filters.purpose === value}
              onChange={(e) =>
                handleFilterChange("purpose", e.target.value)
              }
            />
            {label}
          </label>
        </li>
      ))}
              </ul>
            </div>

            <div className="sidebar__section">
              <h3 className="sidebar__title"> </h3>
              <ul className="sidebar__list">
                {[
                  { value: "all", label: "" },
                  { value: "", label: "" },
                  { value: "", label: "" },
                  { value: " ", label: " " },
                ].map(({ value, label }) => (
                  <li key={value}>
                    <label>
                      <input
                        type="radio"
                        name="shape"
                        value={value}
                        checked={filters.shape === value}
                        onChange={(e) =>
                          handleFilterChange("shape", e.target.value)
                        }
                      />
                      {label}
                    </label>
                  </li>
                ))}
              </ul>
            </div>

            <div className="sidebar__section">
              <h3 className="sidebar__title"></h3>
              <ul className="sidebar__list">
                {[
                  { value: "all", label: "" },
                  { value: " ", label: " " },
                  { value: "-", label: "-" },
                  { value: "", label: "" },
                  { value: "", label: "" },
                ].map(({ value, label }) => (
                  <li key={value}>
                    <label>
                      <input
                        type="radio"
                        name="taste"
                        value={value}
                        checked={filters.taste === value}
                        onChange={(e) =>
                          handleFilterChange("taste", e.target.value)
                        }
                      />
                      {label}
                    </label>
                  </li>
                ))}
              </ul>
            </div>
            <div className="sidebar__section">
              <h3 className="sidebar__title"> </h3>
              <ul className="sidebar__list">
                {[
                  { value: "all", label: "" },
                  { value: "true", label: " " }, // "true"   
                ].map(({ value, label }) => (
                  <li key={value}>
                    <label>
                      <input
                        type="radio"
                        name="hit"
                        value={value}
                        checked={filters.hit === (value === "true")} //   boolean
                        onChange={(e) =>
                          onFilterChange({
                            ...filters,
                            hit: e.target.value === "true" ? true : false, //    boolean
                          })
                        }
                      />
                      {label}
                    </label>
                  </li>
                ))}
              </ul>
            </div>

            <div className="sidebar__reset">
              <button onClick={resetFilters} className="sidebar__reset--btn">
                  
              </button>
            </div>
          </div>
</div>

  
  );
};
