import vitaminsData from "../api/vitamins.json";
import { Product } from "../types";

/**
 *      JSON-.
 * @returns {Promise<Product[]>}    .
 */
export const fetchProducts = async (): Promise<Product[]> => {
  try {
    if (!Array.isArray(vitaminsData)) {
      console.error(":      ", vitaminsData);
      return Promise.reject(new Error("  ."));
    }

    //    (,   `id`  `name`)
    const isValid = vitaminsData.every(
      (item) =>
        typeof item === "object" &&
        "id" in item &&
        "name" in item
    );

    if (!isValid) {
      console.error(":     vitamins.json", vitaminsData);
      return Promise.reject(new Error("  ."));
    }

    return Promise.resolve(vitaminsData as Product[]);
  } catch (error) {
    console.error("   :", error);
    return Promise.reject(new Error("   ."));
  }
};
